import React from 'react';
import PropTypes from 'prop-types';

import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';

import logo from '../../images/logo.svg';
import carrito from '../../images/carrito.svg';

import { withStyles } from '@material-ui/core/styles';
const styles = theme => ({        
    toolbar: {
        justifyContent:'space-between',
    },
    columna: {
        width: 60,
    },
    logo: {
        height: 41, width: 41,
    },
    carrito: {
        height: 36,
    },
    badge: {
        top: 8,
        border: '2px solid',
        padding: '10px 6px',        
        borderColor: theme.palette.secondary.dark,
        fontSize: 14,
    }
});

const Menu = ({ classes }) => {
    return (        
        <AppBar position='sticky'         
        >
            <Toolbar className={classes.toolbar}>                
                <div className={classes.columna}>
                    
                </div>
                
                <IconButton                     
                    color='inherit' aria-label="Menú de opciones"
                >
                    <img className={classes.logo} src={logo} alt='Velvet chocolates' />
                </IconButton>                
                
                <div className={classes.columna}>
                    <IconButton                     
                        color='inherit' aria-label="Carrito de compras"
                    >
                        <Badge badgeContent={<b>8</b>} color="secondary" classes={{ badge: classes.badge}}>
                            <img className={classes.carrito} src={carrito} alt='Carrito de compras' />
                        </Badge>                        
                    </IconButton>
                </div>
            </Toolbar>
        </AppBar>                    
    );
};

Menu.propTypes = {
    classes: PropTypes.object.isRequired,    
};

export default withStyles(styles)(Menu);